	<template>
		<nav>
        <dl v-for="item in index_nav" >
        <router-link :to="item.path">
            <dt class="iconfont" >
                <!-- <i v-if="item.hint.count" v-text="item.hint | get_prompt " :class="'_news-'+item.hint.type"></i> -->
            </dt>
            <dd v-text="item.text"></dd>
            </router-link>
        </dl>
    </nav>
	</template>	
<script>
export default{
	
	data:function(){
	return {
			"index_nav":[{text:"书架", path: {
        path: '/novelList'
    	}
    },
    {text:"下载" ,path: {
        path: '/novelListLocal'
    }
		}]
	}
	}
}

</script>


